<template>
    <input class="my-input" :type="this.type" required @input="handleInput">
</template>

<script>
export default{
    name: 'MyInput',
    props: ['value', "type"],
    data () {
        return {
            content: this.value
        }
    },
    methods: {
        handleInput (e) {
        this.$emit('input', this.content)
        }
    }
}
</script>

<style scoped>
.my-input{
    color: var(--white);
    flex: 1 0 auto;
    background-color: var(--black);
    border: none;
    box-sizing: border-box;
    border: 0.15rem solid var(--black);
    border-radius: 2rem;
    padding: 0 0.5rem;
}
.my-input:focus{
    border: 0.15rem solid var(--lighter-black);
    outline: none;
}
</style>