<template>
    <div id="login">
        <h1 class="view-title">Login</h1>
        <form class="container" @submit.prevent="submit">
            <div class="my-input-group">
                <div class="my-input-desc">Email</div>
                <input v-model="email" class="my-input" type="email" required>
            </div>
            <div class="my-input-group">
                <div class="my-input-desc">Password</div>
                <input v-model="password" class="my-input" type="password" required>
            </div>

            <div v-if="message" style="color: red; text-align: center; margin-bottom: 0.5rem;">{{this.message}}</div>

            <myButton text="Login" type="submit" v-if="!this.loading"/>
            <div class="spinner" v-else></div>
        </form>
    </div>
</template>

<script>
import axios from 'axios';
import myButton from '@/components/myButton.vue'
import { setSession } from '@/utils';
export default {
    name: 'login',
    components: {
        myButton
    },
    data(){
        return {
            email: '',
            password: '',
            loading: false,
            message: undefined
        }
    },
    methods:{
        async submit(){
            const baseURL = this.$store.state.apiURL;
            const payload = {email: this.email, password: this.password};
            this.loading = true;
            try {
                const response = await axios.post(`${baseURL}/user/login`, payload);
            
                this.email = '';

                const session = {
                    token: response.data.token,
                    name: response.data.data.name,
                    email: response.data.data.email
                }

                setSession(session);
                this.$router.push({name:'home'});
            } catch (e) {
                const response = e.response;
                this.message = response.data.message;
            }
            this.loading = false;
            this.password = '';
        }
    }
}
</script>

<style scoped>
.container{
    background-color: var(--gray);
    max-width: 30rem;
    margin: auto;
    padding: 1.5rem;
    border-radius: 2rem;

    display: flex;
    flex-direction: column;
}
.container .my-input-group{
    margin: 1rem;
}
.container .my-button{
    margin: 0.5rem auto 0rem;
}
.view-title{
    margin-left: 0;
    text-align: center;
}

.my-input-group{
    display: flex;
    flex-direction: row;
}
.my-input-group .my-input-desc{
    flex: 0 1 6rem;
    color: var(--white);
}

@media (max-width: 768px) {
    .container{
        padding: 1rem;
    }

    .my-input-group{
        flex-direction: column;
    }
    .my-input-group .my-input-desc{
        flex: 1;
        color: var(--white);
        margin-bottom: 1rem;
        text-align: center;
    }
    .my-input{
        flex: 1;
    }
}
</style>