<template>
    <div id="home">
        <h1 class="view-title">{{this.username}}'s Decks</h1>
        <div class="loading" v-if="this.loading">
            <div class="spinner"></div>
            <div class="text-white" style="text-align:center; margin: 1.5rem;">Retrieving data from the server...</div>
        </div>

        <div v-else>
            <div class="decks" v-if="!decks.length">
                <div class="emoji">ðŸ˜•</div>
                <div :style="`color:var(--white); text-align: center; margin:1rem;`">Oops! There's nothing here</div>
                <div :style="`color:var(--white); text-align: center; margin:1rem; font-size: small;`">You haven't created created any decks</div>
                <myNewDeckField @addition="addDeck"/>
            </div>

            <div class="decks" v-else>
                <TransitionGroup name="decks" tag="div">
                    <myDeck v-for="deck in this.decks" :key="deck._id" :deck_name="deck.name" :deck_count="deck.size" :id="deck._id"
                    @study="study" @browse="browse" @erase="erase" class="my-deck"/>
                </TransitionGroup>
                <myNewDeckField @addition="addDeck"/>
            </div>
        </div>
    </div>
</template>

<script>
import myDeck from '@/components/myDeck.vue'
import myNewDeckField from '@/components/myNewDeckField.vue';
import { authGetRequest, authDeleteRequest, getName } from '@/utils';
export default{
    name: 'home',
    components: {
        myDeck,
        myNewDeckField
    },
    data(){
        return {
            loading: true,
            username: getName(),
            decks: [],
        }
    },
    async mounted(){
        try {
            const response = await authGetRequest('/deck');
            this.decks = response.data.data;
        } catch (e) {
            // console.error(e);
        }
        this.loading = false;
    },
    methods:{
        study(childProxy){
            this.$router.push({name: 'study', params: {id: childProxy.id}});
        },
        browse(childProxy){
            this.$router.push({name: 'deck', params: {id: childProxy.id}});
        },
        async erase(childProxy){
            childProxy.loading = true;
            try{
                const response = await authDeleteRequest(`/deck/${childProxy.id}`);

                // Find the index of the element
                const index = this.decks.findIndex(deck => {
                    return deck._id === childProxy.id;
                });

                if (index !== -1){
                    // Eliminate the element by index
                    this.decks.splice(index, 1);
                }
            }
            catch(e){
                // console.log(e);
            }
            childProxy.loading = false;
        },
        addDeck(newDeck){
            this.decks.push(newDeck)
        }
    }
}
</script>

<style scoped>
.decks-move, .decks-enter-active, .decks-leave-active {
  transition: all 0.5s ease;
}
.decks-enter-from, .decks-leave-to {
  opacity: 0;
  transform: scale(0);
}
.my-deck{
    margin-bottom: 1rem;
}
</style>