<template>
    <Transition>
        <div class="my-new-deck-field" v-if="!this.isFieldVisible">
            <myButton id="add-button" text="+" @click="this.toggleVisibility"/>
        </div>

        <form class="my-deck" v-else @submit.prevent="submit">
            <div class="my-input-group">
                <div class="my-input-desc">Name: </div>
                <input v-model="name" class="my-input" type="text" required ref="my-input">
                <div class="new-deck-nav">
                    <myButton type="submit" text="Create" :disabled="this.loading"/>
                    <myButton type="button" text="Cancel" @click="this.toggleVisibility" :disabled="this.loading" />
                </div>
            </div>
        </form>
    </Transition>
</template>

<script>
import myButton from '@/components/myButton.vue';
import myInput from '@/components/myInput.vue';
import {authPostRequest} from '@/utils'
export default{
    name: 'MyNewDeckField',
    components: {
        myButton,
        myInput
    },
    data(){
        return {
            name: '',
            isFieldVisible: false,
            loading: false
        }
    },
    methods:{
        toggleVisibility(){
            this.isFieldVisible = !(this.isFieldVisible);
        },
        async submit(){
            this.loading = true;

            const payload = {name: this.name}
            this.name = '';

            try{
                const response = await authPostRequest(`/deck`, payload);
                const newDeck = response.data.data;
                this.$emit('addition', newDeck);
            }
            catch(e){
                console.log(e);
            }

            this.loading = false;
        }
    }
}
</script>

<style scoped>
.v-enter-active,
.v-leave-active {
    transition: 0.5s ease;
}
.v-leave-active{
    width: 100%;
    position:fixed;
}
.v-enter-from,
.v-leave-to {
    opacity: 0;
    transform: translateX(30px);
}

#add-button{
    display: block;
    background-color: var(--gray);
    width: 9rem;
    margin: auto;
}
.my-input-group{
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
}
.my-input-group .my-input-desc{
    flex: 0 1 3rem;
    margin: 0rem 1rem;
    color: var(--white);
}
.my-input-group .my-input{
    flex: 1 0 auto;
}
.my-button{
    margin-left: 0.6rem;
}

@media (max-width: 768px) {
    .my-input-group{
        flex-direction: column;
        align-items: center;
    }
    .my-button{
        margin-left: inherit;
        margin: 0.3rem;
    }
    .my-input{
        margin: 0.3rem;
    }
    .my-input-group .my-input-desc{
        margin: 0.3rem;
        flex:auto;
    }
}
</style>