<template>
    <div class="my-deck">
        <div class="deck-name">{{deck_name}} ({{this.deck_count}})</div>

        <div class="deck-nav" v-if="!this.loading">
            <myButton text="Study" @click="emitStudy"/>
            <myButton text="Browse" @click="emitBrowse"/>
            <myButton text="Erase" @click="emitErase"/>
        </div>
        <div class="deck-nav" v-else>
            <div class="spinner"></div>
        </div>
    </div>
</template>

<script>
import myButton from '@/components/myButton.vue'
export default{
    name: 'MyDeck',
    data(){
        return {
            loading: false
        }
    },
    props: ['deck_name', 'deck_count', 'id'],
    components: {
    myButton,
    },
    methods:{
        emitStudy(){
            this.$emit('study', this);
        },
        emitBrowse(){
            this.$emit('browse', this);
        },
        emitErase(){
            this.$emit('erase', this);
        }
    }
}
</script>

<style>
.my-deck{
    background-color: var(--gray);
    color: var(--white);
    border-radius: 2rem;
    padding: 0.5rem;

    display: flex;
    flex-direction: row;
    align-items: center;
}
.deck-name{
    margin-left: 1rem;
    text-overflow: ellipsis;
    white-space: nowrap;
    flex: 0 1 auto;
    overflow: hidden;
}
.deck-nav{
    flex: 1 0 auto;
    margin-right: 0;
    margin-left: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-end;
}
.deck-nav *{
    margin-left: 0.6rem;
}

@media (max-width: 768px) {
    .my-deck{
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .deck-name{
        margin: 0.25rem 0rem 0.5rem;
    }
    .deck-nav{
        align-content: center;
        margin: 0.25rem 0;
    }
}
</style>

<style scoped>
.spinner{
    margin-right: 0.5rem;
    margin-left: auto;
}
@media (max-width: 768px) {
    .spinner{
        margin: auto;
    }
}
</style>