import store from '@/store';
import router from '@/router';
import axios from 'axios';

// Local Storage
function getSession(){
    const stringifiedSession = localStorage.getItem('session');
    try{
        const session = JSON.parse(stringifiedSession);
        if (!session.token || !session.name || !session.email){return undefined}
        return session;
    }catch(e){
        return undefined;
    }
}

function setSession(session){
    const stringifiedSession = JSON.stringify(session);
    localStorage.setItem('session', stringifiedSession);
    store.state.session = session;
}

function eraseSession(){
    localStorage.removeItem('session');
    store.state.session = undefined;
}

// Autheticated Petitions
function getAuthClient(){
    const baseURL = store.state.apiURL;
    const session = store.state.session;
    const token = session.token;

    const axiosClient = axios.create({
        baseURL,
        headers: {'auth-token':token}
    })

    return axiosClient;
}

function handleErrorStatusCodes(e){
    if (e.response.status === 401){
        router.push({name: 'login'});
        eraseSession();
    }else if (e.code === 'ERR_NETWORK'){
        router.push({name: 'service500'});
    }
    return Promise.reject(e);
}

async function authGetRequest(url){
    const axiosClient = getAuthClient();
    try{
        const request = await axiosClient.get(url);
        return request;
    }catch(e){
        return handleErrorStatusCodes(e);
    }
}

async function authDeleteRequest(url){
    const axiosClient = getAuthClient();
    try{
        const request = await axiosClient.delete(url);
        return request;
    }catch(e){
        return handleErrorStatusCodes(e);
    }
}

async function authPostRequest(url, payload){
    const axiosClient = getAuthClient();
    try{
        const request = await axiosClient.post(url, payload);
        return request;
    }catch(e){
        return handleErrorStatusCodes(e);
    }
}

async function authPatchRequest(url, payload){
    const axiosClient = getAuthClient();
    try{
        const request = await axiosClient.patch(url, payload);
        return request;
    }catch(e){
        return handleErrorStatusCodes(e);
    }
}

// Get Session Data
function getName(){
    const session = store.state.session;
    if (session.name){
        return session.name;
    }
    return 'User'
}

// Utils
function shuffle(a) {
    for (let i = a.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [a[i], a[j]] = [a[j], a[i]];
    }
    return a;
}

export {getSession, setSession, eraseSession, authGetRequest, authPostRequest, authPatchRequest, authDeleteRequest, getName, shuffle}