import { createRouter, createWebHashHistory } from 'vue-router'
import store from '../store'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import Service500 from '../views/Service500.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path: '/register',
    name: 'register',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/Register.vue')
    }
  },
  {
    path: '/deck/:id',
    name: 'deck',
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/Deck.vue')
    }
  },
  {
    path: '/study/:id',
    name: 'study',
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/Study.vue')
    }
  },
  {
    path: '/500',
    name: 'service500',
    component: Service500
  },
  {
    path: '/:pathMatch(.*)*',
    redirect: '/'
  },
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

router.beforeEach(async (to, from) => {
  store.state.menuOpen = false;
  if (!store.state.session) {
    // If there is no active session
    if (to.name !== 'login' && to.name !== 'register'){
      return { name: 'login' }
    }
  }
})

export default router
