<template>
    <div id="my-navbar">
        <div id="brand-name">ðŸ§  mem</div>

        <div id="nav" :class="{'active':this.$store.state.menuOpen}">
            <router-link :to="{name:'home'}" class="route" v-if="this.$store.state.session">Home</router-link>
            <!-- <router-link :to="{name:'home'}" class="route">About</router-link> -->

            <router-link :to="{name:'register'}" class="route" v-if="!this.$store.state.session">Register</router-link>
            <router-link :to="{name:'login'}" class="route" v-if="!this.$store.state.session">Login</router-link>
            <a class="route" v-else @click="logOut">Logout</a>
        </div>

        <div class="hamburger-lines" :class="{'active':this.$store.state.menuOpen}" @click="toggleMenu">
            <span class="line line1"></span>
            <span class="line line2"></span>
            <span class="line line3"></span>
        </div>
    </div>
</template>

<script>
import {eraseSession} from '@/utils';
export default {
    name: 'MyNavBar',
    methods:{
        toggleMenu(){
            this.$store.state.menuOpen = !(this.$store.state.menuOpen);
        },
        logOut(){
            this.$router.push({name: 'login'});
            eraseSession();
        }
    }
}
</script>

<style>
#my-navbar{
    color: white;
    padding: 1rem 2rem 0;

    display: flex;
    flex-direction: row;
    align-items: center;
}
#brand-name{
    font-size: 1.5rem;
    text-overflow: ellipsis;
    white-space: nowrap;
    flex: 0 1 auto;
    overflow: hidden;
}
#nav{
    flex: 1 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}
#nav .route{
    font-size: 1.25rem;
    text-decoration:none;
    color: white;
    transition: 0.15s ease;
    cursor: pointer;
}
#nav .route:not(:first-child){
    margin-left: 4.5rem;
}
#nav .route:hover{
    text-decoration: underline;
}

.hamburger-lines {
    display: none;
}

@media (max-width: 768px) {
    #nav{
        position: fixed;
        right: 0;
        top: 0;
        height: 100vh;
        width: 100vw;
        max-width: 0;
        z-index: 1;
        padding-top: 5rem;

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;

        overflow: hidden;
        transition: 0.3s ease;
    }

    #nav a.route{
        display: block;
        visibility: hidden;
        transition: 0.15s ease;

        margin: 1rem !important;
    }

    #nav.active{
        max-width: 100vw;
        /* width: 20rem; */

        background-color: var(--black);
    }

    #nav.active .route{
        visibility:visible;
    }

    .hamburger-lines {
        cursor: pointer;
        height: 1.2rem;
        width: 1.6rem;
        position: fixed;
        top: 2rem;
        right: 2rem;
        z-index: 2;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    .hamburger-lines .line {
        display: block;
        height: 4px;
        width: 100%;
        border-radius: 10px;
        background: rgb(255, 255, 255);
        transition: 0.3s ease;
    }

    .hamburger-lines.active .line{
        position: absolute;
    }

    .hamburger-lines.active .line1{
        transform: rotate(225deg);
    }

    .hamburger-lines.active .line2{
        transform: rotate(-225deg);
    }

    .hamburger-lines.active .line3{
        display: none;
    }
}
</style>